.roundBtn {
    cursor: pointer;
    width: 40px;
    height: 40px;
    border-radius: 20%;
    background-color: rgb(48, 6, 6);
    display: flex;
    align-items: center;
    justify-content: center;
}

.addBtn{
    cursor: pointer;
    width: 60px;
    height: 60px;
    border-radius: 10px;
    background-color: rgb(48, 6, 6);
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid black;
    margin-bottom: 10px;
}